.grey-1{
  background-color:#2B2C2C;
}

.grey-2{
  background-color:#2D2E2E; 
}

.grey-3{
  background-color:#292A2A; 
}

.exp-number i{
  display:none;
}

body{
  font-family: Lato, "Helvetica Neue", Helvetica, Arial, sans-serif;
  line-height: 25px;
  color:#979899;
  font-size: 16px;
  background-color: #2D2E2E;
}

h1{
  color:#E0A80D;
  padding: 42vh 0 0 0;
  font-size:3.9vw;
  line-height: 3.9vw;
}

h2{
  color:#E0A80D;
  font-size:3.9vw;
  line-height: 3.9vw;
  margin-top: 5%;
  text-align: center;
}

h3,h4{
  font-weight: bold;
  margin-bottom: 0;
}

h5{
  font-weight: bolder;
}

ul{
  position:absolute;
  bottom:0;
  width:80%;
  list-style: none;
  border-top: 2px solid #404242;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  padding:20px 0 0 0;
  margin-bottom: 50px;
}

li{
  text-align: center;
  padding:0 7px 0 2px;
}

b{
  font-size: 18px;
}

#my-photo{
  max-width: 90%;
  max-height: 320px;
  border-radius: 50%;
  display:block;
  margin: 0 auto;
}

.my-container{
  width:80%;
  height: 100%;
  min-height: 100vh;
  margin:0 auto;
}

.block-name{
  text-align: right;
  margin:10%;
}

.me{
  width:100%;
  height:100%;
  display:-ms-flexbox;
  display:flex;
  -ms-flex-align: center;
      align-items: center;
  padding:20px 0 20px 0;
}

.head{
  min-height: 500px;
}

.my-biography{
  font-size: 20px;
  line-height: 28px;
  margin:3.5%;
}

.my-expertise{
  margin:8% 5% 0 12%;
}

.exp-name{
  position: relative;
}

.exp-number{
  position:absolute;
  top:0;
  left:-10%;
  color:#E0A80D;
}

.education{
  margin:3% 5% 30px 4%;
}

.education:first-child{
  margin-top:8%;
}

.education:last-child{
  margin-bottom: 4%;
}

.education-map{
  border-left: 2px solid #404242;
  padding-left: 18px;
  position:relative;
}

.point{
  width:10px;
  height: 10px;
  background-color:#E0A80D;
  position:absolute;
  top:-22px;
  left:-6px;
  border-radius: 50%;
}

.ed-year{
  font-size: 12px;
  position:absolute;
  top:-30px;
  color:#595A5B;
}

.ed-location{
  font-size: 12px;
  color:#595A5B;
  margin-top: 2px;
  padding-bottom: 8px;
}

.footer ul{
  margin: 0 auto;
  border-top:none;
  padding-bottom: 20px;
  border-bottom: 2px solid #404242;
  position: static;
}

.footer{
  padding:2% 0 4% 4%;
}

@media (max-width: 991px) {
  h1{ padding-top:20px; }
  h1,h2{font-size: 4.9vw;line-height: 4.9vw}
  .my-container{ min-height: 250px; }
  .block-name{ text-align: center; margin:20px;}
  .port-img img {width:50%; margin-right: 10px; border-radius: 5px; }
  .port-img {margin-top: 20px;border:2px solid transparent; padding:10px;  
    -webkit-border-image: -webkit-linear-gradient(top, #3acfd5 0%, #3a4ed5 100%);  
    -o-border-image: -o-linear-gradient(right, #3acfd5 0%, #3a4ed5 100%);  
       border-image: -webkit-gradient(linear, right top, left top, from(#3acfd5), to(#3a4ed5));  
       border-image: linear-gradient(to left, #3acfd5 0%, #3a4ed5 100%);  
    border-image-slice: 1;  }
  .port-text {width:45%; float:right; height:100%; color:#979899; font-size: 2vw; line-height: 2.5vw;}
  .text h5 {text-align: center; font-size: 3vw; line-height: 3vw;}
}

@media (max-width: 410px) {
  ul{font-size: 8px}
}

@media (min-width: 992px){
      .port-img{
      width:100%;
      max-width: 550px;
      border-radius: 10px;
      overflow: hidden;
      cursor: pointer;
      background-color: black;
      position: relative;
      margin:3% auto;
    }

    .port-img img{
      width:100%;
      -webkit-transition: .5s;
      -o-transition: .5s;
      transition: .5s;
    }

    .port-img:hover img{
      -webkit-transform: rotate(-10deg) scale(1.5);
          -ms-transform: rotate(-10deg) scale(1.5);
              transform: rotate(-10deg) scale(1.5);
      opacity: 0.5;
    }

    .port-text{
      position: absolute;
      top: 3%;
      left: 3%;
      right: 3%;
      bottom: 3%;
    }

    .text {
      color: #fff;
      text-transform: uppercase;
      padding: 0 3%;
      text-align: center;
      opacity: 0;
      -webkit-transition: .5s;
      -o-transition: .5s;
      transition: .5s;
    } 

    .port-img:hover .text{
      opacity:1;
    }

    .text h5 {
      display: inline-block;
      font-size: 25px;
      color: #E0A80D;
      padding-bottom: 3px;
      border-bottom: 2px solid #fff;
      padding-top: 10px;
    }  

    .text p{
      position:absolute;
      font-size: 13px;
      line-height: 16px;
      padding-right: 10px;
    }

    .port-text::before,
    .port-text::after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      -webkit-transition: .5s;
      -o-transition: .5s;
      transition: .5s;
    }  

    .port-text::before{
      border-top: 1px solid #fff;
      border-bottom: 1px solid #fff;
      -webkit-transform: scale(0,1);
          -ms-transform: scale(0,1);
              transform: scale(0,1);
    }

    .port-text::after{
      border-left: 1px solid #fff;
      border-right: 1px solid #fff;
      -webkit-transform: scale(1,0);
          -ms-transform: scale(1,0);
              transform: scale(1,0);
    }

    .port-img:hover .port-text::before,
    .port-img:hover .port-text::after{
      -webkit-transform: scale(1,1);
          -ms-transform: scale(1,1);
              transform: scale(1,1);
    }
}



@media (max-width: 670px) {
  ul{font-size: 12px}
  body{font-size: 14px}
  .my-biography{font-size: 16px}
  
}

@media (min-width: 1120px){
  .exp-number i {display:block; position: absolute; left:-10px;}
}

@media (min-width: 1320px){
  .text p {
    font-size: 18px; 
    line-height: 24px;
  }
}


